.mobileMenu {
    position: fixed;
    z-index: 100;
    display: block;
    box-sizing: border-box;
  
    min-width: 320px;
    max-width: 100%;
    width: 100%;
    height: 100vh;
    padding: 20px;
  
    overflow-y: hidden;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  
    background-color: #fef9f9;
  }
  @media screen and (min-width: 768px) {
    .mobileMenu {
      padding: 32px 32px;
    }
  }
  @media screen and (min-width: 1280px) {
    .mobileMenu {
      width: 1280px;
      padding: 20px 16px;
    }
  }
  
  .head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 28px;
  }
  
  .closeButton {
    cursor: pointer;
    stroke: #ffc107;
    margin-right: 10px;
  }